//Variables
document.addEventListener('DOMContentLoaded', iniciarApp);


function iniciarApp (){
    menuHeader();
    llamarCursos();
}

//Funciones

function menuHeader() {
    const icono = document.getElementById('icono--header');
    const texto = document.getElementById('header--texto--container');
    icono.addEventListener('click', ()=>{
        texto.classList.toggle('active');
    })
}

async function llamarCursos() {

    try {
        
        
        const resultado =  await fetch('../cursos.json');
        const db =  await resultado.json();
        const {cursosMobile} = db;        
        

        //Generar el HMTL

        cursosMobile.forEach(curso =>{
                
            const {Nombre, Nivel, Duración, Img} = curso;

            //DOM Scripting

            //Generar nombre del curso

            const nombreCurso = document.createElement('H3');
            nombreCurso.textContent = Nombre;
            nombreCurso.classList.add('nombreCurso') 

            //Generar el Nivel


            const nivelCurso = document.createElement('P');
            nivelCurso.textContent = `Nivel: ${Nivel}`;
            nivelCurso.classList.add('nivelCurso') 

            //Generar la Duración

            
            const duracionCurso = document.createElement('P');
            duracionCurso.textContent = `Duración: ${Duración}`;
            
            duracionCurso.classList.add('duracionCurso') 

            //Generar la Img

            
            const imgCurso = document.createElement('IMG');
            imgCurso.src = Img;

            const cursosContenedor = document.createElement('DIV');
            cursosContenedor.appendChild(imgCurso);
            cursosContenedor.appendChild(nombreCurso);
            cursosContenedor.appendChild(nivelCurso);
            cursosContenedor.appendChild(duracionCurso);
            cursosContenedor.classList.add('cursoContainer')

            //Inyectar en el HTML

        const containerCursos = document.getElementById('cursos--lista');
        containerCursos.appendChild(cursosContenedor);

    })

    } catch (error) {
        console.log(error)
    }
}